        <v-expansion-panel-content class="pt-3">
          <v-row>
            <v-col md="3" class="pa-2">
              <DateFilter v-on:change-date="getDate" ref="dateFilter" />
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-row class="mt-5 mb-2 ml-1">
      <h3 class="mr-auto block-title">
        <v-icon color="#ff7141" class="mr-2">mdi-cog-outline</v-icon>Оперативное
        регулирование
      </h3>
      <PrintButton />
      <ExportButton :url="createExportUrl" />
    </v-row>
    <DataTable
      :headers="headers"
      :headersFlat="headersFlat"
      :url="url"
      :params="getParams"
      ref="table"
    />
