<script>
import DataTable from '../components/DataTable';
import DateFilter from '../components/DateFilter';
import PrintButton from '../components/PrintButton';
import ExportButton from '../components/ExportButton';
export default {
  name: 'RegulationJoints',
  components: { PrintButton, DateFilter, DataTable, ExportButton },
  watch: {
    dateValue() {
      this.$refs.table.fetchTableData(this.getParams);
    }
  },
  methods: {
    getDate(dateFilter) {
      this.dateValue = dateFilter;
    }
  },
  computed: {
    getParams() {
      return { date: this.dateValue };
    },
    createExportUrl() {
      return 'method=regulation&tab=region-view&date=' + this.getParams.date;
    }
  },
  data() {
    return {
      panel: 0,
      dateValue: Math.floor(Date.now() / 1000),
      url: 'server/splices.json',
      headersFlat: [
        { text: '№ п/п', value: 'sort_number' },
        { text: 'Междорожные стыковые пункты', value: 'long_name' },
        { text: 'на 6:00 ч', value: 'plan_train_half' },
        { text: 'на 18:00 ч', value: 'plan_train' },
        { text: 'на 6:00 ч', value: 'plan_loco_half' },
        { text: 'на 18:00 ч', value: 'plan_loco' },
        { text: 'на 6:00 ч', value: 'plan_train_loco_half' },
        { text: 'на 18:00 ч', value: 'plan_train_loco' }
      ],
      headers: [
        {
          text: '№ п/п',
          align: 'start',
          value: 'sort_number'
        },
        {
          text: 'Междорожные стыковые пункты',
          align: 'start',
          value: 'long_name'
        },
        {
          text: 'План-задание передачи поездов',
          align: 'start',
          children: [
            { text: 'на 6:00 ч', value: 'plan_train_half' },
            { text: 'на 18:00 ч', value: 'plan_train' }
          ]
        },
        {
          text: 'План-задание передачи локомотивов',
          align: 'start',
          children: [
            { text: 'на 6:00 ч', value: 'plan_loco_half' },
            { text: 'на 18:00 ч', value: 'plan_loco' }
          ]
        },
        {
          text: 'План-задание передачи поездо-локомотивов',
          align: 'start',
          children: [
            { text: 'на 6:00 ч', value: 'plan_train_loco_half' },
            { text: 'на 18:00 ч', value: 'plan_train_loco' }
          ]
        }
      ]
    };
  }
};
</script>

