<template>
  <v-btn class="export mt-1" color="#00194d" x-small plain @click="printTable">
    Печать
  </v-btn>
</template>

<script>
export default {
  name: 'PrintButton',
  methods: {
    printTable() {
      var prtContent = document.getElementById('table');
      var WinPrint = window.open('');
      WinPrint.document.write(prtContent.innerHTML);
      WinPrint.document.close();
      WinPrint.print();
    }
  }
};
</script>

