                { text: 'Мт (4-х)', value: 'ft' },
                { text: 'Мт (3-х)', value: 'ft3' },
                { text: 'Мт (2-х)', value: 'fl' }
              ]
            },
            {
              text: 'ПВД',
              children: [
                { text: 'Мт (3-х)', value: 'locl3' },
                { text: 'Мт (2-х)', value: 'locl' }
              ]
            },
            {
              text: 'ТОЛКАЧИ',
              children: [
                { text: 'Мт (4-х)', value: 'pt4' },
                { text: 'Мт (3-х)', value: 'pt' },
                { text: 'Мт (2-х)', value: 'pl' }
              ]
            },
            {
              text: 'ОПЕР, РД',
              children: [
                { text: 'Мт (4-х)', value: 'rt4' },
                { text: 'Мт (3-х)', value: 'rt' },
                { text: 'Мт (2-х)', value: 'rl' }
              ]
            },
            {
              text: 'ВСЕГО',
