<template>
  <div class="pa-3">
    <v-expansion-panels v-model="panel" focusable hover class="rounded-lg">
      <v-expansion-panel id="filter-norm-sections">
        <v-expansion-panel-header color="#4D6C99" class="rounded-lg pa-2">
          <div>
            <v-icon class="mr-1" color="white">mdi-filter-outline</v-icon>
            <span>Фильтры</span>
          </div>
        </v-expansion-panel-header>
        <v-expansion-panel-content class="pt-3">
          <v-row>
            <v-col md="3" class="pa-2">
              <DateFilter v-on:change-date="getDate" ref="dateFilter" />
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-row class="mt-5 mb-2 ml-1">
      <h3 class="mr-auto block-title">
        <v-icon color="#ff7141" class="mr-2">mdi-trending-up</v-icon>Оперативное
        нормирование
      </h3>
      <PrintButton />
    </v-row>
    <DataTable
      :headers="headers"
      :headersFlat="headersFlat"
      :url="url"
      :params="getParams"
      ref="table"
    />
  </div>
</template>

<script>
import DataTable from '../components/DataTable';
import DateFilter from '../components/DateFilter';
import PrintButton from '../components/PrintButton';
export default {
  name: 'NormalizationRatio',
  components: { PrintButton, DateFilter, DataTable },
  watch: {
    dateValue() {
      this.$refs.table.fetchTableData(this.getParams);
    }
  },
  methods: {
    getDate(dateFilter) {
      this.dateValue = dateFilter;
    }
  },
  computed: {
    getParams() {
      let dateToParams = this.dateValue;
      if (typeof this.dateValue === 'string') {
        let dateFromFilter = new Date(this.dateValue);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return { date: dateToParams, drl: 1 };
    }
  },
  data() {
    return {
      panel: 0,
      params: {},
      dateValue: Math.floor(Date.now() / 1000),
      dateFilterId: 'dateNormRatio',
      url: 'server/norm_minmax.json',
      headersFlat: [
        { text: 'Депо приписки локомотивов', value: 'depo' },
        { text: 'Т (4-х), т.ед.', value: 'park_t4' },
        { text: 'Т (3-х), т.ед.', value: 'park_t3' },
        { text: 'Л (2-х), т.ед.', value: 'park_l2' },
        { text: 'Т (4-х)', value: 'neisp_t4' },
        { text: 'Т (3-х)', value: 'neisp_t3' },
        { text: 'Л (2-х)', value: 'neisp_l2' },
        { text: 'Т (4-х), т.ед.', value: 'max_t4' },
        { text: 'Т (3-х), т.ед.', value: 'max_t3' },
        { text: 'Л (2-х), т.ед.', value: 'max_l2' },
        { text: 'Т (4-х)', value: 'uch_t4' },
        { text: 'Т (3-х)', value: 'uch_t3' },
        { text: 'Л (2-х)', value: 'uch_l2' }
      ],
      headers: [
        {
          text: 'Депо приписки локомотивов',
          align: 'start',
          value: 'depo'
        },
        {
          text: 'Инвентарный парк',
          align: 'start',
          cols: 3,
          children: [
            { text: 'Т (4-х), т.ед.', value: 'park_t4' },
            { text: 'Т (3-х), т.ед.', value: 'park_t3' },
            { text: 'Л (2-х), т.ед.', value: 'park_l2' }
          ]
        },
        {
          text: 'Плановая доля неисправных локомотивов',
          align: 'start',
          cols: 3,
          children: [
            { text: 'Т (4-х)', value: 'neisp_t4' },
            { text: 'Т (3-х)', value: 'neisp_t3' },
            { text: 'Л (2-х)', value: 'neisp_l2' }
          ]
        },
        {
          text: 'Макс. содержание',
          align: 'start',
          cols: 3,
          children: [
            { text: 'Т (4-х), т.ед.', value: 'max_t4' },
            { text: 'Т (3-х), т.ед.', value: 'max_t3' },
            { text: 'Л (2-х), т.ед.', value: 'max_l2' }
          ]
        },
        {
          text: '% содержания от общего по участку',
          align: 'start',
          cols: 3,
          children: [
            { text: 'Т (4-х)', value: 'uch_t4' },
            { text: 'Т (3-х)', value: 'uch_t3' },
            { text: 'Л (2-х)', value: 'uch_l2' }
          ]
        }
      ]
    };
  }
};
</script>
